    <a
      href="javascript:;"
      class="nextBtn setGray"
      v-if="Number(cash)<10 && Number(cash)>=0"
      @click="cashToast"
    ></a>
    <p>1.10</p>
    <p>2.3%97%(222)</p>
    <div v-if="show" class="contanines" @touchmove.prevent>
